<template>
	<div>
		<div class="btn">
			<el-button type="primary" @click="addUser">添加用户</el-button>
		</div>

		<el-table :data="tableDate">
			<el-table-column prop="id" label="ID" align="center"></el-table-column>
			<el-table-column prop="username" label="账号" align="center"></el-table-column>
			<el-table-column prop="role" label="角色名" align="center"></el-table-column>
			<el-table-column prop="enable" label="状态" align="center">
				<template slot-scope="scope">
					<span v-text="scope.row.enable == '1' ? '启用' : '禁用'"></span>
				</template>
			</el-table-column>
			<el-table-column label="操作" align="center" width="300px">
				<template slot-scope="scope">
					<el-button type="success" size="mini" @click="handleReset(scope.$index, scope.row)">重置密码</el-button>
					<el-button type="danger" size="mini" @click="handleDel(scope.$index, scope.row)">删除</el-button>
				</template>
			</el-table-column>
		</el-table>
		
		<div class="block">
			<el-pagination @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-size="10" layout="prev, pager, next, jumper"
			 :total="totalPage">
			</el-pagination>
		</div>
		
		<el-dialog title="添加用户" :visible.sync="dialogUser" width="500px" :close-on-click-modal="false">
			<div class="box">
				<el-form :model="form" label-width="80px">
					<el-form-item label="账号">
						<el-input v-model="form.username"></el-input>
					</el-form-item>
					<el-form-item label="密码">
						<el-input v-model="form.password"></el-input>
					</el-form-item>
					<el-form-item label="选择角色">
						<el-select v-model="form.role">
							<el-option v-for="item in rolesList" :key="item.title" :label="item.name" :value="item.title"></el-option>
						</el-select>
					</el-form-item>
					<el-form-item label="选择学校">
					<el-select v-model="form.school_id" placeholder="请选择学校">
						<el-option v-for="item in schoolList" :key="item.id" :label="item.name" :value="item.id">
						</el-option>
					</el-select>
					</el-form-item>
				
					<el-form-item label="选择状态">
						<el-select v-model="form.enable">
							<el-option v-for="item in stateList" :key="item.value" :label="item.label" :value="item.value"></el-option>
						</el-select>
					</el-form-item>
					<div class="submit">
						<el-form-item>
							<el-button type="primary" @click="newUser">提交</el-button>
						</el-form-item>
					</div>
				</el-form>
			</div>
		</el-dialog>
		
		<el-dialog title="修改密码" :visible.sync="dialogResetPassWord" width="500px" :close-on-click-modal="false">
			<div class="box">
				<el-form :model="pwdForm" label-width="120px">
					<el-form-item label="旧密码">
						<el-input v-model="pwdForm.old_password"></el-input>
					</el-form-item>
					<el-form-item label="新密码">
						<el-input v-model="pwdForm.new_password"></el-input>
					</el-form-item>
					<el-form-item label="再次确认新密码">
						<el-input v-model="pwdForm.confirm_password"></el-input>
					</el-form-item>
					<div class="submit">
						<el-form-item>
							<el-button type="primary" @click="ChangePassword">提交</el-button>
						</el-form-item>
					</div>
				</el-form>
			</div>
		</el-dialog>


	</div>
</template>

<script>
	import API from '@/api//index.js'
	
	export default {
		data() {
			return {
				dialogUser: false,
				form: {
					username: '',
					password: '',
					enable: '',
					role: '',
					school_id: ''
				},
				stateList: [{
						label: '启用',
						value: 1
					},
					{
						label: '禁用',
						value: 2
					}
				],
				rolesList: [],
				schoolList: [],
				tableDate: [],
				
				dialogResetPassWord: false, // 修改密码
				pwdForm: {
					new_password: '',
					old_password: '',
					confirm_password: '',
					id: ''
				},
				
				
				
				// 分页
				currentPage: 1,
				totalPage: 0
			}
		},
		mounted() {
			this.getSchool();
			this.getRoles();
			this.getUsers();
		},
		methods: {
			// 获取学校列表
			getSchool() {
				var self = this;
				API.schools(self.currentPage, 100).then(res => {
					self.schoolList = res.data;
				})
			},
			getRoles() {
				var self = this;
				API.roles(self.currentPage).then(res => {
					self.rolesList = res.data;
				})
			},
			getUsers() {
				var self = this;
				API.users(self.currentPage).then(res => {
					self.tableDate = res.data;
					self.totalPage = res.total;
				})
			},
			addUser() {
				var self = this
				self.dialogUser = true;
				self.form = {
					username: '',
					password: '',
					enable: '',
					title: '',
					school_id: ''
				}
			},
			newUser() {
				var self = this;
				API.user(self.form).then(res => {
					self.dialogUser = false;
					self.$message.success('提交成功');
					self.getUsers();
					self.currentPage = 1;
					self.form = {};
				})
			},
			
			// 操作
			handleEdit(index, row) {
				var self = this
				self.dialogUser = true;
				self.form = row;
			},
			// 重置密码
			handleReset(index, row) {
				var self = this
				self.dialogResetPassWord = true;
				self.pwdForm.id = row.id;
			},
			ChangePassword() {
				var self = this;
				if(self.pwdForm.new_password === self.pwdForm.confirm_password) {
					API.resetPassword(self.pwdForm).then(res => {
						self.dialogResetPassWord = false;
						self.$message.success('修改成功');
						self.getUsers();
						self.currentPage = 1;
						self.pwdForm = {};
					})
				} else {
					self.$message.error('新密码与确认密码不一致');
				}
			},
			
			
			
			// 分页
			handleCurrentChange(val) {
				var self = this;
				self.getUsers();
			},
		
		}
	}
</script>

<style scoped>
	.permission {
		display: flex;
		flex-wrap: wrap;
	}
	
	.permission-item {
		margin: 10px;
		padding: 0 10px;
	}
</style>
