<template>
	<div>
		<div class="btn">
			<el-button type="primary" @click="addRole">添加角色</el-button>
		</div>

		<el-table :data="tableDate">
			<el-table-column prop="id" label="ID" align="center"></el-table-column>
			<el-table-column prop="name" label="角色名" align="center"></el-table-column>
			<!-- <el-table-column props="username" label="状态" align="center"></el-table-column> -->
			<!-- <el-table-column prop="permissions" label="操作权限" align="center"> -->
			</el-table-column>
			<el-table-column label="操作" align="center" width="300px">
				<template slot-scope="scope">
					<el-button type="success" size="mini" @click="handleEdit(scope.$index, scope.row)">编辑权限</el-button>
					<el-button type="danger" size="mini" @click="handleDel(scope.$index, scope.row)">删除</el-button>
				</template>
			</el-table-column>
		</el-table>

		<div class="block">
			<el-pagination @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-size="10" layout="prev, pager, next, jumper"
			 :total="totalPage">
			</el-pagination>
		</div>

		<el-dialog title="添加角色" :visible.sync="dialogRole" width="90%" :close-on-click-modal="false">
			<div class="box">
				<el-form :model="form" label-width="80px">
					<el-form-item label="角色名称">
						<el-input v-model="form.name"></el-input>
					</el-form-item>
					<el-form-item label="选择权限">
						<el-checkbox v-model="checkAll" @change="AllChange">全选</el-checkbox>
						<div class="permission">
							<el-checkbox-group v-model="form.permissions" class="permission-item">
								<el-checkbox label="student" @change="oneChange" border>学生管理</el-checkbox>
								<el-checkbox label="infomation" @change="oneChange">学生列表</el-checkbox>
								<el-checkbox label="infomationGet" @change="oneChange">获取学生列表</el-checkbox>
								<el-checkbox label="infomationAdd" @change="oneChange">新增学生</el-checkbox>
								<el-checkbox label="infomationMore" @change="oneChange">批量录入学生</el-checkbox>
								<el-checkbox label="infomationEdit" @change="oneChange">学生编辑</el-checkbox>
								<el-checkbox label="infomationDel" @change="oneChange">学生编辑</el-checkbox>
							</el-checkbox-group>

						</div>
						<div class="permission">
							<el-checkbox-group v-model="form.permissions" class="permission-item">
								<el-checkbox label="grade" @change="oneChange" border>班级管理</el-checkbox>
								<el-checkbox label="gradeGet" @change="oneChange">年级列表</el-checkbox>
								<el-checkbox label="gradeAdd" @change="oneChange">新增年级</el-checkbox>
								<el-checkbox label="gradeDel" @change="oneChange">删除年级</el-checkbox>
								<el-checkbox label="classGet" @change="oneChange">班级列表</el-checkbox>
								<el-checkbox label="classAdd" @change="oneChange">添加班级</el-checkbox>
								<el-checkbox label="studentDetail" @change="oneChange">查看学生</el-checkbox>
								<el-checkbox label="classDel" @change="oneChange">删除班级</el-checkbox>
							</el-checkbox-group>
						</div>
						<div class="permission">
							<el-checkbox-group v-model="form.permissions" class="permission-item">
								<el-checkbox label="family" @change="oneChange" border>家长信息</el-checkbox>
								<el-checkbox label="familyGet" @change="oneChange">家长列表</el-checkbox>
								<el-checkbox label="familyMore" @change="oneChange">查看更多家长</el-checkbox>
							</el-checkbox-group>
						</div>
						<div class="permission">
							<el-checkbox-group v-model="form.permissions" class="permission-item">
								<el-checkbox label="teacher" @change="oneChange" border>教职工管理</el-checkbox>
								<el-checkbox label="teacherGet" @change="oneChange">获取教职工</el-checkbox>
								<el-checkbox label="teacherDetail" @change="oneChange">查看详情</el-checkbox>
								<el-checkbox label="teacherDel" @change="oneChange">删除教职工</el-checkbox>
								<el-checkbox label="teacherAudit" @change="oneChange">教职工审核</el-checkbox>
							</el-checkbox-group>
						</div>
						<div class="permission">
							<el-checkbox-group v-model="form.permissions" class="permission-item">
								<el-checkbox label="permission" @change="oneChange" border>角色管理</el-checkbox>
								<el-checkbox label="roleGet" @change="oneChange">获取角色列表</el-checkbox>
								<el-checkbox label="roleAdd" @change="oneChange">添加角色</el-checkbox>
								<el-checkbox label="roleEdit" @change="oneChange">编辑权限</el-checkbox>
								<el-checkbox label="roleDel" @change="oneChange">删除角色</el-checkbox>
							</el-checkbox-group>
						</div>
						<div class="permission" style="margin-left: 140px;">
							<el-checkbox-group v-model="form.permissions" class="permission-item">
								<el-checkbox label="manageGet" @change="oneChange">管理员列表</el-checkbox>
								<el-checkbox label="manageAdd" @change="oneChange">添加管理员</el-checkbox>
								<el-checkbox label="manageEdit" @change="oneChange">编辑管理员</el-checkbox>
								<el-checkbox label="manageDel" @change="oneChange">删除管理员</el-checkbox>
							</el-checkbox-group>
						</div>
					</el-form-item>
					<div class="submit">
						<el-form-item>
							<el-button type="primary" @click="newRole">提交</el-button>
						</el-form-item>
					</div>
				</el-form>
			</div>
		</el-dialog>


	</div>
</template>

<script>
	import API from '@/api/index.js'
	
	
	export default {
		data() {
			return {
				dialogRole: false,
				form: {
					name: '',
					permissions: []
				},
				stateList: [{
						label: '启用',
						value: 1
					},
					{
						label: '禁用',
						value: 0
					}
				],
				tableDate: [],

				checkAll: false,
				permissionList: [
					"student",	// 学生列表
					"infomation",
					"infomationGet",
					"infomationAdd",
					"infomationMore",
					"infomationEdit",
					"infomationDel",
					
					"grade",	// 班级管理
					"gradeGet",
					"gradeAdd",
					"gradeDel",
					"classGet",
					"classAdd",
					"studentDetail",
					"classDel",
					
					"family",	// 家长信息
					"familyGet",
					"familyMore",
					
					"teacher",	// 教职工管理
					"teacherGet",
					"teacherDetail",
					"teacherDel",
					"teacherAudit",
					
					"permission",	// 角色管理
					"roleGet",
					"roleAdd",
					"roleEdit",
					"roleDel",
					"manageGet",
					"manageAdd",
					"manageEdit",
					"manageDel"
				],
				// 分页
				currentPage: 1,
				totalPage: 0
			}
		},
		mounted() {
			this.getRoles();
		},
		methods: {
			addRole() {
				var self = this
				self.dialogRole = true;
				self.form = {
					name: '',
					permissions: []
				}
			},
			getRoles() {
				var self = this;
				API.roles(self.currentPage).then(res => {
					self.tableDate = res.data;
					self.totalPage = res.total;
				})
			},
			newRole() {
				var self = this;
				API.role(self.form).then(res => {
					self.dialogRole = false;
					self.$message.success('提交成功');
					self.getRoles();
					self.currentPage = 1;
					self.form = {};
					self.form.permissions = [];
				})
			},
			// 操作
			handleEdit(index, row) {
				var self = this
				self.dialogRole = true;
				self.form = row;
				self.form.permissions = row.permissions;
				self.checkAll = row.permissions.length === self.permissionList.length;
				row.permissions
			},
			AllChange(val) {
				var self = this;
				self.form.permissions = val ? self.permissionList : []
			},
			oneChange(val) {
				var self = this;
				self.checkAll = self.form.permissions.length === self.permissionList.length;
			},

			// 分页
			handleCurrentChange(val) {
				var self = this;
				self.getRoles();
			},

		},
	}
</script>

<style scoped>
	.permission {
		display: flex;
		flex-wrap: wrap;
	}

	.permission-item {
		margin: 10px;
		padding: 0 10px;
	}
</style>
